.form {
  $this: &;
  max-width: 450px;
  background-color: #fff;
  text-align: left;
  padding: 45px;
  @include xs {
    max-width: none;
    padding: 20px; }
  &__wrap {
    // text-align: center
    @include xs {
      margin-left: -15px;
      margin-right: -15px;
      padding-left: 15px;
      padding-right: 15px; } }
  &__title {
    margin-bottom: 20px; }
  &__field {
    margin-bottom: 12px; }
  &__btns {
    text-align: center;
    span {
      color: #bbbbbb;
      font-size: 14px;
      line-height: 2; } }

  &--romb {
    margin-left: auto;
    margin-right: auto;
    &__wrap {
      background-image: url('../img/romb.png');
      @include cover;
      background-size: contain;
      padding: 125px 0px;
      @include xs {
        padding: 90px 15px;
        background-size: 500px 500px; }
      @include r(500) {
        padding: 30px 15px;
        background-size: 360px 360px; } } }
  &--romb-sm {
    &__wrap {
      @include xs {
        padding-left: 0;
        padding-right: 0;
        margin-left: 0;
        margin-right: 0; } }
    &__romb-sm {
      z-index: -1;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      @include cover;
      background-size: contain;
      width: 135px;
      height: 149px; } } }

.input {
  position: relative;
  input {
    height: 41px;
    font-family: inherit;
    border: 1px solid  $c-gray;
    padding: 5px 15px;
    color: $c-text;
    width: 100%;
    font-size: 16px;
    @include placeholder {
      font-family: inherit;
      color: $c-text; } }
  label {
    @include light;
    font-size: 16px; }
  .icon {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    right: 15px;
    fill: $c-gray; }

  &--mod {
    input {
      @include placeholder {
        color: $c-gray; } } }
  &--with-label {
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    label {
      width: 90px; } } }


.select {
  position: relative;
  select {
    opacity: 0;
    visibility: hidden;
    position: absolute; }
  &::before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 13px;
    right: 17px;
    @include icon-arrow(10, 10, 1, currentColor, -45); }

  &--mod {
    .custom-select {
      &.has-placeholder {}
      .custom-select {
        &__opener {
          color: $c-gray; }
        &__option {
          &[data-value="placeholder"] {
            display: none; } } } }
    &.has-placeholder-hidden {
      .custom-select {
        &__opener {
          color: $c-text; } } }
    &::before {
      @include icon-arrow(10, 10, 1, $c-gray, -45); } }

  .custom-select {
    user-select: none;
    &.is-open {
      .custom-select {
        &__panel {
          visibility: visible;
          opacity: 1; } } }
    &.has-placeholder {
      .custom-select {
        // &__opener
        //   color: $c-gray
        &__option {
          &[data-value="placeholder"] {
            display: none; } } } }
    // &.has-placeholder-hidden
    //   .custom-select
    //     &__opener
    //       color: $c-text

    &__opener {
      height: 41px;
      border: 1px solid  $c-gray;
      cursor: pointer;
      padding: 8px 15px;
      color: $c-text; }
    &__panel {
      padding-top: 10px;
      padding-bottom: 10px;
      border: 1px solid  $c-gray;
      position: absolute;
      z-index: 1;
      background-color: #fff;
      width: 100%;
      left: 0;
      top: 100%;
      opacity: 0;
      visibility: hidden;
      max-height: 300px;
      overflow: hidden; }

    &__option {
      cursor: pointer;
      padding: 5px 10px;
      transition: color .3s, background-color .3s;
      @include hover {
        color: $c-accent;
        background-color: $c-gray; } }
    &__options {
      overflow: auto;
      max-height: 250px;
      padding-top: 40px; }
    &__panel-item {
      padding-left: 10px;
      padding-right: 10px;
      margin-bottom: 5px;
      position: absolute;
      width: 100%;
      height: 30px;
      left: 0;
      top: 10px;
      overflow: hidden;
      background-color: #fff;
      input {
        border: 1px solid  $c-gray;
        height: 30px;
        width: 100%;
        padding: 5px;
        padding-left: 25px;
        font-family: inherit; }
      .icon {
        fill: $c-gray;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        left: 15px;
        font-size: 16px;
        pointer-events: none; } } } }

