.hero {
  @include cover;
  min-height: calc(100vh - 80px); // header height
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  .container {
    z-index: 1;
    position: relative; } }

.hero-man {
  position: absolute;
  bottom: 0;
  right: 0;
  @include cover;
  background-size: contain;
  background-position: 50% 100%;
  height: 100%;
  width: 40%;
  z-index: 0;
  @include sm {
    display: none; }
  &--sm {
    height: 45%;
    width: 25%;
    right: 20%;
    @include md {
      height: 50%;
      width: 30%;
      right: 10%; } } }

.sun-shining {
  @include cover;
  background-size: contain;
  width: 100%;
  height: 100%;
  position: absolute;
  top: -100px;
  left: 0;
  z-index: 0;
  @include sm {
    display: none; } }
