.h2, h2 {
  text-transform: uppercase;
  color: $c-text;
  font-size: 42px;
  line-height: 1;
  @include bold;
  @include sm {
    font-size: 38px; }
  @include xs {
    font-size: 32px; } }

.h3, h3 {
  font-size: 16px;
  color: $c-accent;
  text-transform: uppercase;
  @include bold; }

.subttl {
  @include light;
  font-size: 14px;
  text-transform: uppercase;
  @include xs {
    font-size: 13px; } }
