@include font('proximanova-regular', 'proximanova-regular-webfont');
@include font('proximanova-light', 'proximanova-light-webfont');
@include font('proximanova-bold', 'proximanova-bold-webfont');
@include font('proximanova-semibold', 'proximanova-semibold-webfont');

@mixin reg {
  font-family: 'proximanova-regular', Arial, sans-serif; }
@mixin light {
  font-family: 'proximanova-light', Arial, sans-serif; }
@mixin bold {
  font-family: 'proximanova-bold', Arial, sans-serif; }
@mixin semi {
  font-family: 'proximanova-semibold', Arial, sans-serif; }

@mixin xl {
  @include r(1499) {
    @content; } }
@mixin lg {
  @include r(1199) {
    @content; } }
@mixin md {
  @include r(991) {
    @content; } }
@mixin sm {
  @include r(767) {
    @content; } }
@mixin xs {
  @include r(575) {
    @content; } }


body {
  background: #FFF;
  line-height: 1.4;
  font-size: 16px;
  @include reg;
  color: $c-text;
  @include no-scroll; }

.out {
  overflow-x: hidden; }

.layout {
  padding-top: 62px;
  &__inner {
    background-color: #fff; } }


.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: $c-accent;
  color: #fff;
  font-size: 16px;
  min-width: 229px;
  border-radius: 13px;
  text-align: center;
  padding: 11px 30px;
  .icon {
    fill: currentColor;
    font-size: 24px;
    margin-left: 10px; }
  &--full {
    min-width: 0; } }

.link {
  @include reg;
  font-size: 14px;
  color: #7ba0fd;
  text-decoration: underline; }
