.header {
  $this: &;
  background-color: #fff;
  height: 80px;
  text-align: center;
  padding-top: 10px;
  position: relative;
  z-index: 10;
  @include xl {
    padding-top: 14px; }
  @include lg {
    height: auto;
    padding-bottom: 14px;
    .col-xl-2 {
      position: static; }
    .container {
      position: relative;
      padding-left: 127px; } } // logo width
  @include xs {
    .container {
      padding-left: 80px; } }
  &__logo {
    margin-top: -10px;
    @include xl {
      margin-top: -14px; }
    @include lg {
      position: absolute;
      top: 0;
      left: 0; }
    @include xs {
      img {
        width: 80px;
        height: 80px; } } }
  &__title {
    text-transform: uppercase;
    font-size: 21px;
    @include light;
    @include xl {
      font-size: 18px; }
    @include xs {
      display: none; } }
  &__subttl {
    @include semi;
    color: $c-accent;
    text-transform: uppercase;
    font-size: 21px;
    @include xl {
      font-size: 18px; }
    @include xs {
      display: none; } }
  &__phone {
    color: #000;
    font-size: 21px;
    @include light;
    @include xl {
      font-size: 18px; }
    @include xs {
      font-size: 16px; }
    strong {
      @include bold; } }
  &__mail {
    font-size: 21px;
    color: #000;
    @include light;
    @include xl {
      font-size: 18px; }
    @include xs {
      font-size: 16px;
      display: inline-block; }
    .icon {
      font-size: 24px;
      @include xl {
        font-size: 21px; }
      @include xs {
        font-size: 18px;
        position: relative;
        top: 5px; } } }
  &__mail {
    display: inline-flex;
    align-items: center;
    text-decoration: underline;
    span {
      display: inline-block;
      font-size: 0;
      margin-right: 10px; } }

  &--cabinet {
    padding: 0;
    height: auto;
    @include xl {
      padding: 0; }
    #{$this}__logo {
      width: 127px;
      margin-top: 0;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1; }
    #{$this}__inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      padding-left: 127px;
      height: 87px; }
    #{$this}__title {
      font-size: 30px;
      color: $c-accent;
      @include bold;
      padding-left: 20px; } } }

.logo {
  display: block;
  font-size: 0; }
